<style scoped>
    li{
        font-size: 20px;
        text-align: center;
    }
    .layout-user, .btn{
        font-size: 11px;
        display: inline-block;
        margin-left: 80px;
        margin-right: 80px;
    }
    .layout-user{
        height: 41px;
        width: 41px;
        background-color: pink;
        border: 1px solid pink;
        border-radius: 25px;
        margin-top: 2px;
        margin-bottom: 0px;
    }
    .layout-search{
        width: 200px;
        height: 30px;
        margin-left: 200px;
        background-color: #265190;
        border: #3879D9 1px solid;
        border-radius: 5px;
        padding: 5px;
        color: #fff;
    }
    .glyphicon{
        margin-left: -27px;
    }
    .navbar-inverse{
        background-color: #255090;
        border-color: #255090;
    }
    .navbar-inverse .navbar-nav > .active > a{
        background-color: #4380DB;
    }
    .navbar-inverse .navbar-nav > .active > a:hover{
        background-color: #3B74C9;
    }
    .login{
        margin-top: 30px;
        text-align: center;
    }
    .login-btn{
        width: 166px;
    }
    .forget{
        margin-left: 100px;
        margin-bottom: 10px;
        margin-top: -10px;
    }
    .register{
        margin-top: 30px;
        text-align: center;
    }
    .ivu-form .ivu-form-item-label{
        text-align: right;
    }
    .input{
        margin-left: 30px;
        padding-right: 30px;
    }
    .reguster-btn{
        margin-left: -30px;
    }
</style>


<template>
  <div class="main">

    <nav class="navbar navbar-inverse" role="navigation">
        <a v-show="window_width>767" class="navbar-brand" href="#">菜鸟教程{{window_width}}</a>
        <input maxlength=12 v-show="window_width>989" class="navbar-btn layout-search" type="text" placeholder="search...">
        <span v-show="window_width>989" class="glyphicon glyphicon-search"></span>
        <ul class="nav navbar-nav navbar-right">
            <li><a href="#">题库</a></li>
            <li class="active"><a href="#">学习路径</a></li>
            <li><a href="#/login">导航</a></li>
            <li>
                <button v-if="login"  @click="modal_login = true" type="button" class="navbar-btn btn btn-info navbar-right">登 陆</button>
                <div v-else class="bottom">
                    <Poptip trigger="hover" placement="bottom" width="100">
                        <div type="ghost" class="layout-user navbar-right"></div>
                        <div class="api" slot="content">
                            <Button @click="login = true" type="warning">退出登陆</Button>
                        </div>
                    </Poptip>
                </div>
            </li>
        </ul>
    </nav>


    <Modal v-model="modal_login" width="400">
        <Menu  mode="horizontal" theme="light" active-name="1">
            <Menu-item name="1">
                <div @click="state = true" >
                登 陆
                </div>
            </Menu-item>
            <Menu-item name="2">
                <div @click="state = false" >
                注 册
                </div>
            </Menu-item>
        </Menu>
        <!-- 登陆 -->
        <div v-show="state">
            <Form class="login" ref="formInline" :model="formInline" :rules="ruleInline" inline>
                <Form-item prop="user">
                    <Input type="text" v-model="formInline.user" placeholder="Username">
                        <Icon type="ios-person-outline" slot="prepend"></Icon>
                    </Input>
                </Form-item>
                <br>
                <Form-item prop="password">
                    <Input type="password" v-model="formInline.password" placeholder="Password">
                        <Icon type="ios-locked-outline" slot="prepend"></Icon>
                    </Input>
                </Form-item>
                <div class="forget">
                    忘记密码？
                </div>
                <Form-item>
                    <Button class="login-btn" type="primary" @click="handleSubmit('formInline')">登录</Button>
                </Form-item>
            </Form>
        </div>
        <!-- 注册 -->
        <div v-show="!state">
            <Form class="register" ref="formCustom" :model="formCustom" :rules="ruleCustom" :label-width="80">
                <Form-item label="用 户 名:" class="input" prop="user">
                    <Input type="text" v-model="formCustom.user" number></Input>
                </Form-item>
                <Form-item label="密 码:" class="input" prop="passwd">
                    <Input type="password" v-model="formCustom.passwd"></Input>
                </Form-item>
                <Form-item label="确认密码:" class="input" prop="passwdCheck">
                    <Input type="password" v-model="formCustom.passwdCheck"></Input>
                </Form-item>
                <Form class="reguster-btn">
                    <Button type="primary" @click="handleSubmit('formCustom')">提交</Button>
                    <Button type="ghost" @click="handleReset('formCustom')" style="margin-left: 8px">重置</Button>
                </Form>
            </Form>
        </div>
        <div slot="footer">
            <!-- <Button v-if="state">登 陆</Button>
            <Button v-if="!state">注 册</Button> -->
        </div>
    </Modal>

  </div>
</template>

<script>
export default {
    name: 'main',
    data () {
        const validatePass = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请输入密码'));
            }else if(value.length < 6){
                callback(new Error('密码长度不能小于6位'));
            }else {
                if (this.formCustom.passwdCheck !== '') {
                    // 对第二个密码框单独验证
                    this.$refs.formCustom.validateField('passwdCheck');
                }
                callback();
            }
        };
        const validatePassCheck = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请再次输入密码'));
            } else if (value !== this.formCustom.passwd) {
                callback(new Error('两次输入密码不一致!'));
            } else {
                callback();
            }
        };
        const validateUser = (rule, value, callback) => {
            if (!value) {
                return callback(new Error('账号不能为空'));
            }
            // 模拟异步验证效果"^[A-Za-z0-9]+$"
            setTimeout(() => {
                var patt=new RegExp("^[A-Za-z0-9]+$");
                if (!patt.test(value)) {
                    callback(new Error('用户名为字母加数字组成'));
                } else {
                    if (value.length < 18) {
                        callback(new Error('用户名为18个字符以内'));
                    } else {
                        callback();
                    }
                }
            }, 1000);
        };
        return {
            login: !true,
            window_width: window.innerWidth,
            modal_login: false,
            state: true,
            formCustom: {
                passwd: '',
                passwdCheck: '',
                user: ''
            },
            formInline: {
                user: '',
                password: '',
            },
            ruleInline: {
                user: [
                    { required: true, message: '请填写用户名', trigger: 'blur' }
                ],
                password: [
                    { required: true, message: '请填写密码', trigger: 'blur' },
                    { type: 'string', min: 6, message: '密码长度不能小于6位', trigger: 'blur' }
                ],
            },
            ruleCustom: {
                passwd: [
                    { validator: validatePass, trigger: 'blur' }
                ],
                passwdCheck: [
                    { validator: validatePassCheck, trigger: 'blur' }
                ],
                user: [
                    { validator: validateUser, trigger: 'blur' }
                ]
            }
        }
    },
    methods: {
        handleSubmit(name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.$Message.success('提交成功!');
                } else {
                    this.$Message.error('表单验证失败!');
                }
            })
        },
        handleReset (name) {
            this.$refs[name].resetFields();
        }
    }
}
</script>





